import request from '@/utils/request'

//分销员默认佣金比例
export function getSysCommissionRate(data) {
  return request({
    url: `/sys/business/shopInfo/getShopConfigInfo?shopId=${data.shopId}`,
    method: 'get',
    data: data
  })
}

//分销员设置佣金
export function setSysCommissionRate(data) {
  return request({
    url: `/sys/business/shopInfo/updateShopConfigInfo`,
    method: 'post',
    data: data
  })
}


//分销员商品列表
export function fetchList(data) {
  return request({
    url: '/sys/business/channelCommission/getCommissionProductList',
    method: 'post',
    data: data
  })
}



//设置分销商品佣金比例
export function setProductRate(data) {
  return request({
    url: `/sys/business/channelCommission/insertChannelProductConfig`,
    method: 'post',
    data: data
  })
}

