<!--
 * @Description: 佣金设置
 * @Author: your name
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div>
    <!-- 筛选条件 -->
    <el-card shadow="never">
        <h3>分销员默认佣金设置
          <span class="item-contain-red">
            佣金比例与邀请奖励比例之和不能大于100%，可保留两位小数
          </span>
        </h3>
      <el-form label-width="130px"
               style="width:50%;display:inline-block">
        <el-form-item label="佣金比例"
                      style="margin-top:20px;">
          <el-input-number v-model="commissionRate"
                           :min="0.01"
                           :max="99"
                           :precision="2"
                           :controls="false"
                           size="mini"
                           style="width:80px">
          </el-input-number>%
          <span class="item-contain-red">
            输入范围最小为0.01，可保留2位小数
          </span>
        </el-form-item>
        <el-form-item label="邀请奖励佣金比例">
          <el-input-number v-model="inviteRate"
                           :min="0.01"
                           :max="99"
                           :precision="2"
                           :controls="false"
                           size="mini"
                           style="width:80px">
          </el-input-number>%
          <span class="item-contain-red">
            输入范围最小为0.01，可保留2位小数
          </span>
        </el-form-item>

        <div style="margin-top:40px">
          <el-button type="primary"
                     @click="saveRule">保存</el-button>
          <el-button @click="getRule">取消</el-button>
        </div>

      </el-form>
      <div class="pic">
        <img src="../../../assets/images/distribution/commission.png"
             alt="">
      </div>
    </el-card>

    <el-alert title="商品佣金比例：分销员推广商品后获得的商品佣金=商品佣金比例*商品实付金额（扣除物流、余额、积分等）；"
              type="warning"
              :closable="false"></el-alert>
    <el-alert title="邀请奖励比例：下级分销员推广后，上级分销员获得邀请奖励=邀请奖励比例*商品实付金额（扣除运费、余额、积分等）；"
              type="warning"
              :closable="false"></el-alert>
    <el-alert title="举例：B是A发展的分销员，B成功推广商品后，B可获得商品佣金，A可获得邀请奖励；"
              type="warning"
              :closable="false"></el-alert>
  </div>
</template>
<script>
import { getSysCommissionRate, setSysCommissionRate } from '@/api/distributionManagement/commission'
export default {
  props: ["status","shop","user"],
  data() {
    return {
      commissionRate: undefined,
      inviteRate: undefined
    }
  },
  created() {
    this.getRule()
  },
  mounted() {

  },
  methods: {
    getRule() {
      getSysCommissionRate({
        shopId: this.shop.shopId
      }).then(res => {
        if (res.code == 200) {
          this.commissionRate = res.data.commissionRate
          this.inviteRate = res.data.inviteRate
        }
      })
    },
    saveRule() {
      if (this.commissionRate == undefined || this.commissionRate === '') {
        this.$notify.error({
          title: '错误',
          message: '请输入佣金比例'
        });
        return
      }
      if (this.inviteRate == undefined || this.inviteRate === '') {
        this.$notify.error({
          title: '错误',
          message: '请输入邀请奖励佣金比例'
        });
        return
      }
      let rateNum = Number(this.inviteRate) + Number(this.commissionRate)
      if (rateNum > 100) {
        this.$notify.error({
          title: '错误',
          message: '佣金比例与邀请奖励比例之和不能大于100%'
        });
        return
      }
      setSysCommissionRate({
        shopId: this.shop.shopId,
        commissionRate: this.commissionRate,
        inviteRate: this.inviteRate
      }).then(res => {
        if (res.code == 200) {
          this.$notify({
            title: '成功',
            message: '默认佣金设置成功',
            type: 'success'
          });
          this.getRule()
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message
          });
        }
      })
    },
  },
  computed: {
    startTime() {
      return this.status;
    },
  },
  // watch: {
  //   startTime(newValue, oldValue) {
  //     this.getRule()
  //     console.log(newValue)
  //     return newValue;
  //   },
  // }
}
</script>

<style lang="scss" scoped>
.app-container {
  // padding: 20px 20px 20px 20px;
  .item-contain-red {
    color: red;
    margin-left: 30px;
    font-size: 14px;
  }
  .pic {
    display: inline-block;
    width: 350px;
    img {
      width: 350px;
    }
  }
}
</style>
